	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/02/crh0206.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"crh0206.jpg: \" /></p>\n<p>After it was too wet last weekend to work on our <a href=\"/2000/01/29\">mole/gopher problem</a>, we were finally able to take action today. We opened up all the molehills, squirted in the mole repellent (which has an odor like concentrated hog manure), and then trampled down the hills. The ground was so wet, I'm sure the moles and gophers had already drowned.</p>\n<p><a href=\"http://www.cnnsi.com/soccer/world/news/2000/02/06/german_roundup_ap/\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/02/hsv_bayern.jpg\" height=\"152\" width=\"150\" border=\"0\" alt=\"hsv_bayern.jpg: \" /><br />\nHamburg SV 0 Bayern Munich 0</a>. Despite the ban on beer in the stands, HSV keeps pace with the Bundesliga leader.</p>","fields":{"title":"Watching the World Go Round","date":"06 Feb 2000","url":"https://papascott.de/2000-02-06-watching-the-world-go-round/"}}},"pathContext":{"slug":"/2000-02-06-watching-the-world-go-round/","prev":{"fields":{"title":"The New Boss","date":"05 Feb 2000","slug":"/2000-02-05-the-new-boss/"}},"next":{"fields":{"title":"Swamped at Work!","date":"07 Feb 2000","slug":"/2000-02-07-swamped-at-work/"}}}}

